import { getHistoryAPI, getUrlHash } from '../utils/browser';

const initialState = {
  highlights: ['java'],
  role: getUrlHash() || '',
};

const SET_HIGHLIGHTS = 'SET_HIGHLIGHTS';
const TOGGLE_HIGHLIGHT = 'TOGGLE_HIGHLIGHT';
const SET_ROLE = 'SET_ROLE';

export const toggleHighlightAction = highlight => ({
  type: TOGGLE_HIGHLIGHT,
  highlight,
});

export const setHighlightsAction = (highlights = []) => ({
  type: SET_HIGHLIGHTS,
  highlights,
});

export const setRoleAction = role => ({
  type: SET_ROLE,
  role,
});

export default (state = initialState, action) => {
  switch (action.type) {
    case SET_ROLE:
      getHistoryAPI().pushState({}, action.role, `/#${action.role}`);
      return { ...state, role: action.role };
    case TOGGLE_HIGHLIGHT:
      if (state.highlights.includes(action.highlight)) {
        const index = state.highlights.indexOf(action.highlight);
        return {
          ...state,
          highlights: [
            ...state.highlights.slice(0, index),
            ...state.highlights.slice(index + 1, state.highlights.length),
          ],
        };
      }
      return { ...state, highlights: [...state.highlights, action.highlight] };
    case SET_HIGHLIGHTS:
      return { ...state, highlights: action.highlights };
    default:
      return state;
  }
};
